<template>
    <b-container>
        <b-row class="pl-3">
            <h3>FAQ:</h3>
        </b-row>
        <b-list-group>

            <b-list-group-item v-for="qa in model" class="flex-column align-items-start">
                <div class="d-flex w-100 justify-content-between">
                    <h5 class="mb-1">{{qa.question}}</h5>
                </div>
                <p class="mb-1" t>{{qa.answer}}</p>
            </b-list-group-item>

        </b-list-group>
    </b-container>
</template>

<script>
    export default {
        name: "QuestionList",
        data(){
            return {
                model: [
                    {"question": " -   ?", "answer": "!"},
                    {"question": "     ?", "answer": ",         ()."},
                    {"question": " ,     ?", "answer": "    \"  \",     ' !"},
                    {"question": "      ?", "answer": ",   ,      :)"},

                ]
            }
        },
    }
</script>

<style scoped>

</style>


